import React, { Component } from "react";
import { connect } from "react-redux";
import {
  getNavBrandState,
  getNavLinkState,
} from "../../store/reselect";
import slide2 from "../../assets/nurse.jpg";

import View from "../../presentations/programs/detail";
import { FooterBrandBody } from "../../utils/data";
import { withRouter } from 'react-router-dom';

class ProgramDetail extends Component {
  state = {
    src: slide2,
  };

  componentDidMount() {
    const id = this.props.location?.state?.id
    this.props.getProgram(id);
  }

  render() {
    let {
      state: { src },
      props: { brand, navlinks, program },
    } = this;
    return (
      <View
        brand={brand}
        img={src}
        navlinks={navlinks}
        FooterBrandBody={FooterBrandBody}
        program={program}
      />
    );
  }
}

const mapStateToProps = (state) => ({
  brand: getNavBrandState(state),
  navlinks: getNavLinkState(state),
  program: state.programs.single,
});

const mapDispatchToProps = (dispatch) => {
  return {
    getProgram: (data) => dispatch({ type: "SINGLE-PROGRAM" , payload: data}),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ProgramDetail));
