import React from "react";
import PropTypes from "prop-types";
import Header from "../components/headers";
import Footer from "../components/footer";

function index(props) {
  const { program, brand, img, navlinks, FooterBrandBody } = props;
  return (
    <>
      <Header
        brand={brand}
        image={img}
        navItems={navlinks}
        title={program?.title}
        body={`published on ${program?.publishAt}`}
        height="40vh"
        bodyCenter
        center
      />
      <div className="container">
        <div className="mt-5 mb-4">{program?.content}</div>
      </div>
      <Footer navItem={navlinks} brandBody={FooterBrandBody} />
    </>
  );
}

index.propTypes = {
  post: PropTypes.array,
  accessToken: PropTypes.string,
  onNewsDelete: PropTypes.func,
};

export default index;
